body {
  color: #000;
  letter-spacing: .015em;
  margin: 0;
  font-family: Poiret One, cursive;
  font-size: 28px;
  font-weight: 300;
  line-height: 1.71;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

.section {
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
}

.link {
  text-transform: none;
  color: inherit;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.hero__title {
  width: 100%;
  justify-content: center;
  align-items: baseline;
  gap: 25px;
  font-size: 38px;
  display: flex;
}

.hero__title--modified {
  color: #68513f;
  font-family: Style Script, cursive;
  font-size: 60px;
  display: block;
  transform: rotate(356deg);
}

.hero {
  text-align: center;
  background-color: #e7ded8;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: auto;
  margin-right: auto;
  padding-top: 30px;
  padding-bottom: 30px;
}

.search-form {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

.name-input {
  width: 100%;
  height: 30px;
  text-align: center;
  border: 1px solid #e7ded8;
  border-radius: 20px;
  outline: rgba(0, 0, 0, 0);
  padding: 7px 7px 7px 15px;
  font-family: inherit;
  font-size: 24px;
  display: block;
}

.search-btn {
  height: 35px;
  text-transform: uppercase;
  width: 50px;
  height: 46px;
  color: #f5f0f0;
  text-shadow: 0 1px #0f3354;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  border-radius: 100%;
  font-weight: bold;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  animation: pulse 1s cubic-bezier(.6, 0, 0, 1) infinite;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 0 rgba(220, 146, 90, .7), inset 0 1px #ecb998;
}

.search-btn:hover {
  fill: #68513f;
  animation: none;
}

@keyframes pulse {
  to {
    box-shadow: 0 0 0 23px rgba(218, 131, 81, 0), inset 0 1px #e6c49e;
  }
}

.gallery-list {
  flex-wrap: wrap;
  margin: -10px 0 30px -10px;
  padding: 0;
  list-style: none;
  display: flex;
}

.gallery__item {
  background-color: #e7ded8;
  border-radius: 6px;
  margin-top: 10px;
  margin-left: 10px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

@media screen and (min-width: 768px) {
  .gallery__item {
    flex-basis: calc(50% - 10px);
  }
}

@media screen and (min-width: 1200px) {
  .gallery__item {
    flex-basis: calc(25% - 10px);
  }
}

.gallery__item:hover {
  box-shadow: 1px 8px 20px gray;
}

.gallery__thumb {
  max-width: 370px;
  height: 220px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery__img {
  height: 94%;
  width: 94%;
  object-fit: cover;
}

.box-info {
  padding: 5px;
}

.gallery-info {
  height: 42px;
  background-color: #f1ebe7;
  border-radius: 6px;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.gallery-info__text {
  text-align: center;
  flex-direction: column;
  font-size: 12px;
  font-weight: 600;
  display: flex;
}

.is-hidden, .load-more.is-hidden {
  display: none;
}

.load-more {
  min-width: 160px;
  letter-spacing: 2px;
  color: #68513f;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  align-items: center;
  gap: 10px;
  margin: 0 auto 40px;
  padding: 5px 10px;
  font-size: 24px;
  font-weight: bold;
  transition: box-shadow .15s ease-in-out;
  display: flex;
  box-shadow: inset 0 0 40px 40px #e7ded8, 0 0 #e7ded8;
}

.load-more:hover, .top-btn:hover {
  color: #68513f;
  box-shadow: inset 0 0 10px #917763, 0 0 10px 4px #917763;
}

.load-more:disabled {
  color: #fff;
  cursor: not-allowed;
  box-shadow: inset 0 0 40px 40px #a5a4a5, 0 0 #a5a4a5;
}

.spinner {
  width: 30px;
  height: 30px;
  animation: rotate 2s linear infinite;
}

.spinner .path {
  stroke: #ecb593;
  stroke-linecap: round;
  animation: dash 1.5s ease-in-out infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1 150;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 90 150;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 90 150;
    stroke-dashoffset: -124px;
  }
}

.label {
  font-size: 16px;
}

.top-btn {
  z-index: 99;
  color: #fff;
  background-color: #e7ded8;
  border: 1px solid #fff;
  border-radius: 30px;
  outline: none;
  padding: 10px;
  font-size: 18px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 15px;
}

